<template>
  <div class="home">
    <div class="room-controls">
      <input
        v-model="roomCode"
        placeholder="Î∞© ÏΩîÎìú ÏûÖÎ†• (6ÏûêÎ¶¨)"
        maxlength="6"
        pattern="\d*"
        @input="validateInput"
      />
      <button
        @click="handleRoom"
        :disabled="roomCode && !isValidCode"
      >
        {{ isValidCode ? 'Î∞© ÎßåÎì§Í∏∞/Ï∞∏Ïó¨ÌïòÍ∏∞' : 'ÏÉàÎ°úÏö¥ Î∞© ÎßåÎì§Í∏∞' }}
      </button>
    </div>
    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import axios from 'axios';

// axios Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ±
const api = axios.create({
  baseURL: `${process.env.VUE_APP_API_URL}/api`,
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json'
  }
});

export default {
  name: 'HomeView',
  data() {
    return {
      roomCode: '',
      error: ''
    }
  },
  computed: {
    isValidCode() {
      return /^\d{6}$/.test(this.roomCode);
    }
  },
  methods: {
    validateInput() {
      this.roomCode = this.roomCode.replace(/[^\d]/g, '');
    },
    async handleRoom() {
      this.error = '';

      try {
        if (!this.roomCode) {
          // ÏÉàÎ°úÏö¥ Î∞© ÏÉùÏÑ± (Î∞© ÏΩîÎìú ÏóÜÏùÑ Îïå)
          const response = await api.post('/rooms');
          if (response.data && response.data.roomCode) {
            this.saveRecentRoom(response.data.roomCode);
            this.navigateToRoom(response.data.roomCode);
          }
        } else if (this.isValidCode) {
          // 6ÏûêÎ¶¨ Ïà´ÏûêÎ•º ÏûÖÎ†•ÌñàÏùÑ Îïå ÏÉà Î∞© ÏÉùÏÑ±
          const response = await api.post('/rooms', { roomCode: this.roomCode });
          if (response.data && response.data.roomCode) {
            this.saveRecentRoom(response.data.roomCode);
            this.navigateToRoom(response.data.roomCode);
          }
        }
      } catch (error) {
        if (error.response) {
          this.showError(error.response.data.message || 'ÏÑúÎ≤Ñ Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        } else if (error.request) {
          this.showError('ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§.');
        } else {
          this.showError('Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        }
        console.error('Error:', error);
      }
    },
    showError(message) {
      this.error = message;
    },
    saveRecentRoom(roomCode) {
      const recentRooms = JSON.parse(localStorage.getItem('recentRooms') || '[]');
      if (!recentRooms.includes(roomCode)) {
        recentRooms.unshift(roomCode);
        if (recentRooms.length > 5) recentRooms.pop();
        localStorage.setItem('recentRooms', JSON.stringify(recentRooms));
      }
    },
    navigateToRoom(roomCode) {
      // Ï§ëÎ≥µ Ïù¥Îèô Ïò§Î•ò Ï≤òÎ¶¨
      this.$router.push(`/room/${roomCode}`).catch(err => {
        if (err.name !== 'NavigationDuplicated') {
          throw err;
        }
      });
    }
  }
}
</script>

<style scoped>
.home {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
}

.room-controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

input {
  flex: 1;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  padding: 8px 16px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.error-message {
  color: #ff0000;
  margin-top: 10px;
}
</style>
